import { Component, ElementRef, ViewChild } from '@angular/core';
// Пути к переводам
import { header } from './data/data-paths';
// AOS
import { aosInit } from './scripts/aos';
// Сервис шапки
import { HeaderService } from 'src/app/services/header.service';
// Сервис переводчика
import { TranslaterService } from './services/translate.service';
// preloader
import { preloaderInit } from './scripts/preloader';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
})
//
//
//
export class AppComponent {
  constructor(
    public headerService: HeaderService,
    public translater: TranslaterService
  ) {}

  // Пути
  headerText = header;
  @ViewChild('sidenav')
  sidenav!: ElementRef | any;
  aboba(el: any) {
    el
      ? (document.body.style.overflow = 'hidden')
      : (document.body.style.overflow = 'auto');
  }

  ngOnInit() {
    aosInit();
    preloaderInit();
    window.addEventListener('resize', () => {
      preloaderInit();
    });
  }
}
