import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { AppRoutingModule } from './app-routing.module';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { Routes, RouterModule } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
// Компоненты
import { AppComponent } from './app.component';
import { HeaderComponent } from './components/header/header.component';
import { HomePageComponent } from './components/home-page/home-page.component';
import { FooterComponent } from './components/footer/footer.component';

// Tranlsate
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';

// Angular material
import { MatInputModule } from '@angular/material/input';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatGridListModule } from '@angular/material/grid-list';
import { MatSidenavModule } from '@angular/material/sidenav';
import { MatSelectModule } from '@angular/material/select';
import { LayoutModule } from '@angular/cdk/layout';
import { FullscreenComponent } from './components/home-page/fullscreen/fullscreen.component';
import { SkillsComponent } from './components/home-page/skills/skills.component';
import { GameComponent } from './components/home-page/game/game.component';
import { AngularGame } from './scripts/game';
import { SwiperModule } from 'swiper/angular';
import { ContactsComponent } from './components/home-page/contacts/contacts.component';
import { ThreeDComponent } from './components/home-page/three-d/three-d.component';
import { WorksPageComponent } from './components/works/works-page/works-page.component';
import { WorkComponent } from './components/works/work/work.component';
import { MatAutocompleteModule } from '@angular/material/autocomplete';
export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}
// Марширутизация
const appRoutes: Routes = [
  { path: 'home', component: HomePageComponent },
  { path: 'works', component: WorksPageComponent },
  { path: 'contact', redirectTo: '/home#contacts' },
  { path: '**', redirectTo: '/home' },
];

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    HomePageComponent,
    FooterComponent,
    FullscreenComponent,
    SkillsComponent,
    GameComponent,
    ContactsComponent,
    ThreeDComponent,
    WorksPageComponent,
    WorkComponent,
  ],
  imports: [
    SwiperModule,
    BrowserModule,
    AppRoutingModule,
    RouterModule.forRoot(appRoutes, {
      anchorScrolling: 'enabled',
    }),
    BrowserAnimationsModule,
    MatSidenavModule,
    MatSelectModule,
    HttpClientModule,
    MatGridListModule,
    MatInputModule,
    MatAutocompleteModule,
    FormsModule,
    ReactiveFormsModule,
    MatFormFieldModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient],
      },
    }),
    LayoutModule,
  ],
  providers: [AngularGame],
  bootstrap: [AppComponent],
})
export class AppModule {}
