import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

@Injectable({
  providedIn: 'root',
})
export class TranslaterService {
  constructor(public translate: TranslateService) {
    this.definitionLang(location.hash);
    /*Ставлю язык*/ this.languageChange(this.language);
  }

  //Все языки
  all_language: string[] = ['EN', 'RU'];

  // Язык / По дефолту EN
  language: string = 'EN';

  // Определение языка по hash
  definitionLang(hash: string) {
    if (hash === '#EN') this.language = 'EN';
    if (hash === '#RU') this.language = 'RU';
  }

  // Метод смены языка
  languageChange(lang: string): void {
    this.translate.use(lang.toLowerCase());
    location.hash = lang.toUpperCase();
  }
}
