import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { Observable, debounceTime, map, startWith } from 'rxjs';
// переводы
import { works } from 'src/app/data/data-paths';
// работы
import { WORKS } from 'src/app/data/works';
//
import { Iwork } from 'src/app/data/interfaces';
//
import { PreloaderService } from 'src/app/services/preloader.service';

@Component({
  selector: 'app-works-page',
  templateUrl: './works-page.component.html',
  styleUrls: ['./works-page.component.scss'],
})
export class WorksPageComponent implements OnInit {
  // текст
  text = works;
  // работы
  works = WORKS;

  // наполняем массив подсказками
  fillWorksNames() {
    this.values = this.works.map((e) => {
      return e.mainInfo.name;
    });
  }
  //
  constructor(private PreloaderService: PreloaderService) {}

  // Переменные для подсказок
  search: any = new FormControl('');
  values: string[];
  filteredOptions: Observable<string[]>;

  notFound: boolean = false;

  // Проверка работ по поиску

  checkWork(work: Iwork): boolean {
    let workName: string = work.mainInfo.name.toLowerCase(),
      workStack: string[] = work.mainInfo.stack,
      inputValues = this.search.value.split(','), // Значения интупа если через , ; #html,#scss
      haveValue = 0; // Сколько совпадения, должно соответсвовать количестиву параметров
    // Задаем для каждого значения проверку
    for (let value of inputValues) {
      let check = this.checkInputValue(value.trim(), workName, workStack);
      if (check) haveValue++;
    }

    if (this.haveWork()) {
      this.notFound = false;
    } else {
      this.notFound = true;
    }
    return haveValue == inputValues.length;
  }
  // Ищем работы по значению инпута
  checkInputValue(input: string, name: string, stack: string[]): boolean {
    if (
      input === '' ||
      name.includes(input.toLowerCase()) ||
      (input.slice(0, 1) === '#' && this.checkStack(stack, input))
    ) {
      return true;
    }
    return false;
  }
  // Поиск среди стэка
  checkStack(stack: string[], input: string): boolean {
    let result: boolean = false;
    for (let el of stack) {
      let include = el.toLowerCase().includes(input.toLowerCase());
      if (include) {
        result = true;
        break;
      }
    }
    return result;
  }
  // Проверка, найдены ли работы по поиску
  haveWork(): boolean {
    let allWorks = Array.from(document.querySelectorAll('.works__box'));
    allWorks.length -= 1;

    let result = false;
    for (let work of allWorks) {
      if (work.classList.contains('visible')) {
        result = true;
        break;
      }
    }
    return result;
  }
  // Инициализируем компонент
  ngOnInit(): void {
    // Идем к верху
    window.scrollTo(0, 0);
    // Создаем массив подсказок
    this.fillWorksNames();
    // добавляем подсказки
    this.filteredOptions = this.search.valueChanges.pipe(
      startWith(''),
      map((value: string) => this._filter(value || ''))
    );
    //preloader
    this.PreloaderService.show_preloader();
  }

  //

  ngAfterContentChecked() {
    console.clear();
  }
  ngAfterViewInit() {
    setTimeout(() => {
      this.PreloaderService.hide_preloader();
    }, 0);
  }
  // Фильтруем значения инпута
  private _filter(value: string): string[] {
    const filterValue = value.toLowerCase();
    return this.values.filter((option) =>
      option.toLowerCase().includes(filterValue)
    );
  }
}
