import { Component } from '@angular/core';
// text
import { threeD } from 'src/app/data/data-paths';
// 3d model script
import { threeD as initThreeD } from 'src/app/scripts/spline';
//
import { PreloaderService } from 'src/app/services/preloader.service';
@Component({
  selector: 'app-three-d',
  templateUrl: './three-d.component.html',
})
export class ThreeDComponent {
  text = threeD;
  constructor(private PreloaderService: PreloaderService) {}
  ngOnInit() {
    this.PreloaderService.show_preloader();
    initThreeD().then((result) => {
      if (result) {
        this.PreloaderService.hide_preloader();
      }
    });
  }
}
