import Typed from 'typed.js';
// typed
export function typed() {
  let options = {
    strings: ['', 'web', 'html', 'css', 'js', 'git', 'ts', 'and', 'more'],
    typeSpeed: 200,
    backSpeed: 200,
    loop: true,
    showCursor: true,
    cursorChar: '|',
    backDelay: 800,
    smartBackspace: false,
  };
  new Typed('.word', options);
}
