import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class PreloaderService {
  constructor() {}
  load: boolean = false;
  preloader: Element = document.querySelector('#preloader');

  show_preloader() {
    this.load = true;
    this.preloader.classList.add('visible');
  }
  hide_preloader() {
    this.load = false;
    this.preloader.classList.remove('visible');
  }
  ngOnInit() {}
}
