import { Component } from '@angular/core';
import { AngularGame } from '../../../scripts/game';
import { game as text } from 'src/app/data/data-paths';
import { retry } from 'rxjs';
import { WorkComponent } from '../../works/work/work.component';
interface Tables {
  x: number;
  y: number;
}

@Component({
  selector: 'app-game',
  templateUrl: './game.component.html',
})
export class GameComponent {
  constructor(public game: AngularGame) {}

  // Текст
  text = text;
  // параметры
  // уровень
  lvl = 1;
  // массив эмодзиков для поиска
  findItems!: string[];
  // сетка доски
  board: Tables = {
    x: 0,
    y: 0,
  };
  // общеее количество элементов
  allItems!: number;
  // последний элемент по которому кликнули
  lastClickEl: HTMLDivElement | null = null;
  // включение игры
  gameEnable = true;
  // Все карты
  allCards: Element[] | null;
  // Победа
  isWon: boolean = false;

  //▰▰▰▰▰▰▰▰▰ Методы компонента ▰▰▰▰▰▰▰▰▰▰
  // Метод получения элементов
  getFindItems(lvl: number) {
    this.findItems = this.game.getElements(lvl);
  }
  // Метод получинеия параметров игрового поля
  getBoardSettings(lvl: number) {
    // Получаем размер площадки
    this.board = this.game.getTable(lvl);
    // Количесво элементов
    this.allItems = this.board.x * this.board.y;
  }
  // Метод обновления игрового поля
  update(lvl: number) {
    if (lvl > 10) {
      this.gameEnable = false;
      this.lvl = 10;
      this.won();
      return;
    }
    this.lvl = lvl;
    this.findItems = [];
    this.lastClickEl = null;
    this.allCards = null;
    this.gameEnable = true;
    this.isWon = false;
    setTimeout(() => {
      this.getFindItems(lvl);
      this.getBoardSettings(lvl);
    }, 0);
  }

  // Метод клика по карточке
  clickOnCard(e: HTMLDivElement): void {
    // проверка на активность игры / Если карта уже найденная
    if (
      !this.gameEnable ||
      e.classList.contains('found') ||
      (this.lastClickEl && e.id === this.lastClickEl.id)
    )
      return;
    // Наполняем массив всех карточек
    if (!this.allCards)
      this.allCards = Array.from(document.querySelectorAll('.body-game__card'));
    // затем проверка есть ли уже активная карта
    if (!this.lastClickEl) {
      this.lastClickEl = e;
      e.classList.add('active');
      return;
    }
    //если есть активная карта выполняем череду действий

    // добавляем класс
    e.classList.add('active');
    // проверяем на совпадение
    if (!this.isPairFound(e, this.lastClickEl)) {
      this.clearCard(400);
    }
    // все ли карты найдены
    if (this.isAllFound()) {
      this.update(++this.lvl);
    }
  }
  // найшли ли пару
  isPairFound(lastEL: HTMLDivElement, nowEl: HTMLDivElement): boolean {
    let lastContent = lastEL.querySelector('.body-game__card-back').textContent,
      nowContent = nowEl.querySelector('.body-game__card-back').textContent;
    if (lastContent === nowContent) {
      [lastEL, nowEl].forEach((el) => {
        el.classList.add('found');
      });
      this.lastClickEl = null;
      return true;
    }
    return false;
  }
  // все ли пары найдены
  isAllFound(): boolean {
    let result = true;
    this.allCards.forEach((e) => {
      if (!e.classList.contains('found')) {
        result = false;
      }
    });
    return result;
  }
  // снятие класса активности
  clearCard(delay_ms: number) {
    this.gameEnable = false;
    setTimeout(() => {
      // удаляем классы через 400мс
      this.allCards.forEach((e) => {
        if (e.classList.contains('active')) e.classList.remove('active');
      });
      // активируем игру
      this.gameEnable = true;
    }, delay_ms);

    this.lastClickEl = null;
  }
  // Все уровни пройдены
  won() {
    this.isWon = true;
  }
  ngOnInit() {
    this.update(this.lvl);
  }
}
