// random array перемешивает массив
function arrayRandom(array: string[]) {
  for (let i = array.length - 1; i > 0; i--) {
    let j = Math.floor(Math.random() * (i + 1));
    [array[i], array[j]] = [array[j], array[i]];
  }
}

// Наполнение массива для игры
function fillArray(count: number, fill: string[]) {
  let fillArr: string[] = [];
  filling(count, fill);

  function filling(count: number, fill: string[]) {
    arrayRandom(fill);
    if (fill.length < count) {
      fillArr = fillArr.concat(fill);
      count -= fill.length;
      // если элементов меньше чем полей, то по новой
      filling(count, fill);
    } else {
      fillArr = fillArr.concat(fill.slice(0, count));
    }
  }
  return fillArr;
}

// Все размеры поля
let tableslvl = [
  { x: 2, y: 2 } /*1 lvl*/,
  { x: 3, y: 2 } /*2 lvl*/,
  { x: 4, y: 2 } /*3 lvl*/,
  { x: 4, y: 3 } /*4 lvl*/,
  { x: 4, y: 3 } /*5 lvl*/,
  { x: 4, y: 4 } /*6 lvl*/,
  { x: 4, y: 4 } /*7 lvl*/,
  { x: 4, y: 4 } /*8 lvl*/,
  { x: 4, y: 5 } /*9 lvl*/,
  { x: 6, y: 6 } /*10 lvl*/,
];

// класс игры
export class AngularGame {
  constructor() {}
  // Элементы поиска
  items: string[] = [
    '🍉',
    '🥕',
    '🍒',
    '🥝',
    '🍍',
    '🥥',
    '🍋',
    '🥩',
    '🍔',
    '🍗',
    '❔',
    '❓',
    '🎃',
    '🏐',
    '💻',
    '🔪',
    '💣',
    '🔫',
  ];
  // Метод получения массива элементов поиска
  getElements(lvl: number) {
    //  Маштаб поля на нынешнем лвл-е
    let nowTable: any = tableslvl[lvl - 1];

    // Количесво пар при этом маштабе
    let pairCount = (nowTable.x * nowTable.y) / 2;

    // Массив который будет отправлен
    let sendArr: string[] = [];

    // Получаем массив предметов поиска
    sendArr = fillArray(pairCount, this.items);
    // Повторяем массив для получения пар
    sendArr = sendArr.concat(sendArr);
    // Возвращаем перемешаный массив
    arrayRandom(sendArr);
    return sendArr;
  }
  getTable(lvl: number) {
    return tableslvl[lvl - 1];
  }
}
