// spline
import { Application } from '@splinetool/runtime';

export async function threeD() {
  const canvas: any = document.getElementById('my3d');
  let isLoad = false;
  if (canvas) {
    const app = new Application(canvas);
    await app
      .load('https://prod.spline.design/8BD8t40kZSfHrkwa/scene.splinecode')
      .then(
        () => {
          isLoad = true;
        },
        (err) => {
          isLoad = false;
        }
      );
  }
  return isLoad;
}
