import { Component, Input, OnInit } from '@angular/core';
// work interface
import { Iwork } from 'src/app/data/interfaces';
// mobile
import { IsphoneService as isphone } from 'src/app/services/isphone.service';
// переводы
import { works } from 'src/app/data/data-paths';
@Component({
  selector: 'app-work',
  templateUrl: './work.component.html',
  styleUrls: ['./work.component.scss'],
})
export class WorkComponent implements OnInit {
  constructor(private isphone: isphone) {}
  text = works;
  @Input() work: Iwork;
  needClass = 'active';
  phone: boolean;
  // Клик или наведние на блок
  workActivate(work: HTMLDivElement, e: Event) {
    let eventType = e.type;

    if (eventType === 'mouseover') work.classList.add(this.needClass);
    else if (eventType === 'mouseleave') work.classList.remove(this.needClass);
    else if (this.phone && eventType === 'click')
      work.classList.toggle(this.needClass);
  }

  ngOnInit(): void {
    this.phone = this.isphone.isPhone();
  }
}
