import { Component, ViewChild } from '@angular/core';

// Contacts
import { slides } from 'src/app/data/slides-info';

// Swiper
import { SwiperOptions } from 'swiper';
import { SwiperComponent } from 'swiper/angular';
import { Clipboard } from '@angular/cdk/clipboard';

// isPhone
import { IsphoneService } from 'src/app/services/isphone.service';

// Text
import { contacts } from 'src/app/data/data-paths';

@Component({
  selector: 'app-contacts',
  templateUrl: './contacts.component.html',
})
export class ContactsComponent {
  constructor(public clipboard: Clipboard, public isPhone: IsphoneService) {}
  // Настройки swiper
  slides = slides;

  // текст для перевода
  contacts = contacts;

  config: SwiperOptions = {
    slidesPerView: 3,
    spaceBetween: 50,
    centeredSlides: true,
    initialSlide: 1,
    loop: true,
    grabCursor: true,
    touchRatio: 0.7,
    breakpoints: {
      0: {
        centeredSlides: false,
        slidesPerView: 1,
      },
      1024: {
        centeredSlides: true,
        slidesPerView: 3,
      },
    },
  };

  @ViewChild('swiper', { static: false }) swiper?: SwiperComponent;
  slideNext() {
    this.swiper.swiperRef.slideNext(300);
  }
  slidePrev() {
    this.swiper.swiperRef.slidePrev(300);
  }
  // Логика компонента
  addClass(el: HTMLElement, ev: any) {
    let isActiveSlide = el.parentElement.classList.contains(
      'swiper-slide-active'
    );
    let isNotActive = !el.classList.contains('active');
    let isCopy = ev.target.classList.contains('copy');

    if ((isActiveSlide && isNotActive) || isCopy) el.classList.add('active');
    else el.classList.remove('active');
  }
  RemoveClasses() {
    let allSlides = Array.from(document.querySelectorAll('.slide-contacts'));
    let isActive = (el) => {
      return el.classList.contains('active');
    };
    allSlides.map((el) => {
      if (isActive(el)) el.classList.remove('active');
    });
  }
  copyThis(text: string, notification: HTMLDivElement) {
    this.clipboard.copy(text);
    notification.classList.add('show');
    setTimeout(() => {
      notification.classList.remove('show');
    }, 1000);
  }
  ngOnInit() {
    this.isPhone.isPhone();
  }
}
